import React from 'react'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { Link } from 'react-router-dom'

const MenuItem = (props) => {
  let typeIcon = props.title === "Home" ? "fas" : "far";
  return (
    <li>
      <Link className="group block py-3" to={props.path}>
        <span className="group-hover:bg-green-100 group-hover:text-green-500 py-3 pr-8 pl-4 rounded-full font-ms font-bold text-xl">
          <FontAwesomeIcon
            size="lg"
            className="mr-7"
            icon={[typeIcon, props.icon]}
          />
          {props.title}
        </span>
      </Link>
    </li>
  );
}

export default MenuItem;
